@import'animate.css';@font-face{font-family:"Louis George Cafe";src:url("../fonts/Louis-George-Cafe.ttf")}@font-face{font-family:"Louis George Cafe Light";src:url("../fonts/Louis-George-Cafe-Light.ttf")}@font-face{font-family:"Zachery";src:url("../fonts/Zachery.otf")}h1,h2,h3,h4{font-family:"Zachery";user-select:none}h1{text-transform:uppercase;letter-spacing:2px}p{font-family:"Louis George Cafe";user-select:none;text-align:justify}.p-global{padding:0 10vw 0 10vw}.nav{background-color:#fff;color:#9d9d9c;display:flex;justify-content:center;align-items:stretch;gap:2rem;height:100px}.list{padding:0;margin:0;list-style:none;display:flex;gap:3rem;font-family:"Zachery";text-transform:uppercase;font-size:20px}.nav a{color:inherit;text-decoration:none;height:100%;display:flex;align-items:center}.nav a,.nav li{transition:.3s}.nav a ::before,.nav li ::before{content:"â€¢";margin-right:45px;color:#d9d9d9;cursor:default}.nav li.active{color:#9c4c1e}.nav li.active ::before{color:#d9d9d9}.nav li:hover,.nav a:hover{color:#bf6937}.nav li:hover ::before,.nav a:hover ::before{color:#d9d9d9}.btn{display:none}@media screen and (max-width: 500px){.nav{height:100%;justify-content:space-between;position:sticky;top:0;z-index:2;border-bottom-left-radius:20px;border-bottom-right-radius:20px;box-shadow:8px 8px 19px -4px rgba(0,0,0,.38)}.list{flex-direction:column;height:auto;padding-left:10%}.items{width:100%;border-top:1px solid rgba(255,255,255,.555);text-align:center;margin-right:0px;padding:20px 0}.nav a ::before,.nav li ::before{content:"";margin-right:0}.btn{display:block;height:50px;width:50px;margin:15px;position:-webkit-sticky;position:fixed;right:0;cursor:pointer;border:1px solid #000;border-radius:10px;background-color:#fff}.btn-svg{font-size:30px}}